<template>
<div class="mobile-page cynews-container">
    <MainLayout :title="$t('Common.CYNew.Title')" :leftIcon="`LEFT_ARROW`" :leftAction="() => this.$router.go(-1)">
        <div class="padding-content">
            <InputSearch :search="fetchSearchCYNews" />
            <div class="items-CY">
                <template v-for="(cynew, index) in cynews">
                    <CYNewItem :cynew="cynew" :key="index" />
                </template>
                <InfiniteLoading :isSearch="isSearch" :isError="isError" :isComplete="isComplete" :fetchData="fetchCYNews" :classParent="'.items-CY'" />
            </div>
        </div>
    </MainLayout>
</div>
</template>

<script>
import MainLayout from '@/components/Common/Layout/MainLayout'
import CYNewItem from '@/components/CYNew/CYNewItem'
import InputSearch from '@/components/Common/Input/InputSearch'
import InfiniteLoading from '@/components/Common/Loading/InfiniteLoading'
import {
    CODE_BBS
} from '@/constants'
import {
    SHOW_POPUP_BUTTON
} from '@/mutations/Common.mutation'
import {
    FETCH_CYNEWS,
    CREATE_CYNEWS
} from '@/mutations/CYNew.mutation'
export default {
    name: 'CYNews',
    components: {
        MainLayout,
        CYNewItem,
        InputSearch,
        InfiniteLoading
    },
    data: function () {
        return {
            page: 0,
            isComplete: false,
            isError: false,
            isSearch: false,
            searchValue: ''
        }
    },
    computed: {
        cynews() {
            return this.$store.state.CYNew.CYNews;
        },
        totalCYNew() {
            return this.$store.state.CYNew.totalCYNew;
        }
    },
    async created() {
        await this.$store.dispatch("CREATE_CYNEWS");
    },
    methods: {
        async fetchSearchCYNews(searchValue = '') {
            await this.resetData();
            this.searchValue = searchValue;
            this.isSearch = true;
        },
        async fetchCYNews() {
            try {
                this.page += 1;
                let params = {
                    bbsCd: CODE_BBS.CY_NEW,
                    pageNum: this.page + '',
                    searchValue: this.searchValue,
                }

                await this.$store.dispatch("FETCH_CYNEWS", params);
                if (this.totalCYNew == 0 || this.cynews.length == this.totalCYNew) {
                    this.isComplete = true;
                }
                this.isSearch = false;
            } catch (error) {
                this.isError = true;
            }
        },
        resetData() {
            this.$store.dispatch('CREATE_CYNEWS')
            this.page = 0;
            this.isComplete = false;
            this.isError = false;
        }
    },

    beforeDestroy() {
        this.searchValue = '';
        this.resetData();
    }
}
</script>

<style lang="scss">
.cynews-container {
    .input-search {
        background-color: #F5F5F5;
        position: absolute;
        width: 100%;
    }
}
</style>
