<template>
<div class="item" v-if="cynew" @click="viewCYNewDetail">
    <div class="item-title">{{cynew.artclSbj ? cynew.artclSbj : ''}}</div>
    <div class="item-header d-flex justify-content-between">
        <div class="time">{{cynewTime}}</div>
    </div>
</div>
</template>

<script>
import moment from 'moment'

export default {
    name: 'CYNewItem',
    components: {},
    data: function () {
        return {
            cynewTime: null,
        }
    },
    props: {
        cynew: null
    },
    async created() {
        if (this.cynew.regDt) {
            this.cynewTime = moment(this.cynew.regDt).format("YYYY.MM.DD HH:mm");
        }
    },
    methods: {
        viewCYNewDetail() {
            if (this.cynew.artclSeq) {
                this.$router.push(`/cynew/${this.cynew.artclSeq}`)
            }
        }
    }
}
</script>